import React, { PureComponent } from 'react'
import "./App.css"
import Controls from './components/Controls'

import TaskForm from './components/TaskForm'
export default class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      tasks: [],
      isShowAdd: false,
      taskEditting: null,
      filter: {
        name: '',
        status: -1
      },
      keyword: '',
      setItem:'',
      setSort:0,
    }
  }

  componentDidMount() {
 this.onSort('name',0)
    this.updateList()
  }
  
  updateList = () => {
    if (localStorage && localStorage.getItem('tasks')) {
      var tasks = JSON.parse(localStorage.getItem('tasks'));
      this.setState({
        tasks: tasks
      })

    }
  }
  s4() {
    return Math.floor((1 + Math.random()) * 0x10000).toString(16).substring(1);
  }
  generateID() {
    return this.s4() + this.s4() + '_' + this.s4() + '_' + this.s4()
  }

  showAddWork = (params) => {
    this.setState({
      isShowAdd: params,
      taskEditting: null
    })
    this.onUpdate()
  }
  onClose = () => {
    console.log("vv")
    this.setState({
      isShowAdd: false,
      taskEditting: null
    })
  }
  onResetForm = () => {
    this.setState({
      isShowAdd: false

    })
  }
  onSubmit = (data) => {
    let { tasks } = this.state
    console.log(data.id)
    if (data.id === '' && data.name !== '') {
      data.id = this.generateID()
      tasks.push(data);

    }
    else {
      let index = this.findindex(data.id);
      tasks[index] = data
    }
    this.setState({
      tasks: tasks,
      taskEditting: null
    })
    localStorage.setItem('tasks', JSON.stringify(tasks))
    this.onClose();
    this.updateList()
  }
  onUpdate = async (id) => {
    let { tasks } = this.state;
    let index = this.findindex(id);
    let taskEdit = tasks[index]
    console.log(taskEdit)
    this.setState({
      taskEditting: taskEdit
    })
    this.updateList()
    await this.onResetForm()
    this.onShowForm()
  }
  onShowForm = () => {
    this.setState({
      isShowAdd: true
    })

  }
  onDelete = (id) => {
    let { tasks } = this.state
    let index = this.findindex(id)
    if (index !== -1) {
      tasks.splice(index, 1)
      this.setState({
        tasks: tasks
      });
      localStorage.setItem('tasks', JSON.stringify(tasks))
    }
    this.onUpdate()
    this.updateList()
  }
  onUpdateStatus = (id) => {
    let { tasks } = this.state
    let index = this.findindex(id)
    if (index !== -1) {
      tasks[index].status = !tasks[index].status
      this.setState({
        tasks: tasks
      });
      localStorage.setItem('tasks', JSON.stringify(tasks))
    }
    this.updateList()
  }
  findindex = (id) => {
    let tasks = JSON.parse(localStorage.getItem('tasks'))
    var result = -1;
    tasks.forEach((task, index) => {
      if (task.id === id) {
        result = index
      }
    })
    return result
  }
  onFilter = (filterName, filterStatus) => {
    filterStatus = parseInt(filterStatus, 10)
    this.setState({
      filter: {
        name: filterName.toLocaleLowerCase(),
        status: filterStatus
      }
    })
  }
  onSearch = (keyword) => {
    this.setState({
      keyword: keyword.toLocaleLowerCase()
    })
  }
  onSort = (a,b)=>{
    
    let setItem = a
    let setSort = b
    this.setState({
      setItem:setItem,
      setSort:setSort
    })
   console.log(this.state.setItem, this.state.setSort)
   this.updateList()
   console.log('c')
  }
  render() {
    let { tasks, isShowAdd, taskEditting, filter, keyword,setItem, setSort } = this.state;
    if (filter) {
      if (filter.name) {
        tasks = tasks.filter((task) => {
          return task.name.toLocaleLowerCase().indexOf(filter.name) !== -1
        })
      }
      tasks = tasks.filter((task) => {
        if (filter.status === -1) {
          return task
        }
        else {
          return task.status === (filter.status === 1 ? true : false)
        }
      })

    }
    if(keyword){
      tasks = tasks.filter((task)=>{
        return task.name.toLocaleLowerCase().indexOf(keyword)!==-1
      })
    }
    if(setItem==='name'){
      tasks =tasks.sort((a,b)=>{
        if(a.name>b.name) return setSort
        else if(a.name<b.name) return setSort
        else return 0
      })
    }
    let elmtaskForm = isShowAdd ? <TaskForm
      onSubmit={this.onSubmit}
      onCloseForm={this.onClose}
      task={taskEditting} /> : "";
    return (
      <div className='App'>
        <h1>Quản lý công việc</h1>
        <div className="container">
          <div className="taskform">

            {elmtaskForm}
          </div>
          <div className="controls">

            <Controls
              tasks={tasks}
              onShowAdd={this.showAddWork}
              isShowAdd={isShowAdd}
              onUpdateStatus={this.onUpdateStatus}
              onDelete={this.onDelete}
              onUpdate={this.onUpdate}
              onFilter={this.onFilter}
              onSearch={this.onSearch}
              onSort={this.onSort}
            />
          </div>

        </div>
      </div>
    )
  }
}

