import React, { PureComponent } from 'react'
import "../style/showListWork.css"
import Sort from './Sort'
import Search from './Search'
import TaskList from './TaskList'
export default class Controls extends PureComponent {
	
	handleShow = (e)=>{
		if(!e){
			e=!e
		}
		this.props.onShowAdd(e)	
	}
	render() {		
		let {tasks, isShowAdd} = this.props
		return (
			<div className={isShowAdd?'showListWork w-700':'showListWork w-1000'}>
           
			<button onClick={()=>this.handleShow(isShowAdd)} className=' btn btnAddWork'>
			<i className="fas fa-plus"></i>
			  <span>Thêm công việc</span>
			</button>
			<div className='controlAddWork'>
				<Search onSearch={this.props.onSearch}/>
				<Sort onSort = {this.props.onSort}/>

			</div>
			<TaskList onFilter={this.props.onFilter} onUpdate = {this.props.onUpdate} onDelete ={this.props.onDelete} onUpdateStatus = {this.props.onUpdateStatus} tasks ={tasks} />
		      </div>
		)
	}
}
