import React, { PureComponent } from 'react'
import "../style/addWork.css"
export default class TaskForm extends PureComponent {
  constructor(props){
    super(props)
    this.state={
      id:'',
      name:'',
      status:false
    }
  }
componentDidMount=()=>{
   if(this.props.task){
   this.setState({
     id:this.props.task.id,
     name:this.props.task.name,
     status:this.props.task.status
   })
  }

 }
  onclose = ()=>{
    this.props.onCloseForm()
  }
  onChange=(event)=>{
    let target = event.target
    let name = target.name
    let value = target.value
    if(name==='status'){
      value = target.value==='true'?true:false
    }
      this.setState({
        [name]:value
      })  
  }
  onSubmit = (event)=>{
    event.preventDefault()
    this.props.onSubmit(this.state)
    this.props.onCloseForm()
  }
  onClear = ()=>{
    this.setState({
      name:'',
      status:false
    })
  }
	render() {
    let {id} =this.state 
		return (
			<form  onSubmit={this.onSubmit} className="addWork">
          <div className="add__title">
            <h2 className='add__title--add'>{id!==''?'Cập nhật công việc':'Thêm công việc'}</h2> 
            <i onClick={this.onclose} className="fas fa-window-close"></i>
          </div>
          <div className="add__info">
         <label >Tên:</label>
         <input
          className='name'
          type="text"
          id="name" 
          name='name'
          value={this.state.name}
          onChange={this.onChange}/>
         <label >Trạng thái:</label>
         <select
          name="status"
          id="status"
          value={this.state.status}
          onChange={this.onChange}
          >
           <option value={true}>Kích hoạt</option>
           <option value={false}>Ẩn</option>
         </select>
        
          </div>
          <div className="add__control">
           <button type='submit' className='btn__save'>
           <i className="fas fa-plus"></i>
            <span className="control__title">Lưu lại</span>
           </button>
           <button onClick={this.onClear} className='btn__cancel'>
           <i className="fas fa-times"></i>
           <span className="control__title">Hủy bỏ</span>
           </button>
         </div>
          </form>
		)
	}
}
