import React, { Component } from 'react'
import "../style/sort.css"
export default class Sort extends Component {
	constructor(props){
		super(props)
		this.state={
		 block:false
		}
	      }
	   showSort = (a,b)=>{
		this.props.onSort(a,b)
	   }  
	render() {
		const handleShow=(e)=>{
			e.preventDefault();
			this.setState({block:!this.state.block})
			
		}
		
		return (
			<div className='dropdown'>
			<button onClick={handleShow}  className='btn btnSort'>
			<i className='fas fa-sort'></i>
				Sắp xếp
				</button >
				<ul style={{display:this.state.block?"block":"none"}}  className='dropdown-menu'>
					<li onClick={()=>this.showSort('name',1)}>
						<span className='sort__selected'>
						Từ A-Z
						</span>
						</li>
					<li onClick={()=>this.showSort('name',-1)}>
						<span href='#' className='sort__selected'>
						Từ Z-A
						</span>
						</li>
				</ul>
				</div>

		)
	}
}
